
html {
    height: 100%;
}
body {
    margin:0;
    padding:0;
    font-family: sans-serif;
    background: linear-gradient(#141e30, #243b55);
}

.diary_container {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 400px;
    padding: 40px;
    transform: translate(-50%, -50%);
    background: rgba(0,0,0,.5);
    box-shadow: 0 15px 25px rgba(0,0,0,.6);
    border-radius: 10px;
}

.question_container .question_type {
    margin: 0 0 30px;
    padding: 0;
    color: #fff;
    text-align: center;
}
#question_answer {
    white-space: break-spaces;
    display: flex;
    font-size: 16px;
    color: #fff;
    border: 1px solid #fff;
    background: #e1e1dc;
    width: 100%;
    overflow-wrap: break-word;
}

.question_container form a {
    position: relative;
    display: flex;
    padding: 10px 20px;
    color: #03e9f4;
    font-size: 16px;
    text-decoration: none;
    text-transform: uppercase;
    overflow: hidden;
    transition: .5s;
    margin-top: 40px;
    justify-content: center;
    letter-spacing: 4px
}

.question_container a:hover {
    background: #03e9f4;
    color: #fff;
    border-radius: 5px;
    box-shadow: 0 0 5px #03e9f4,
    0 0 25px #03e9f4,
    0 0 50px #03e9f4,
    0 0 100px #03e9f4;
}

.question_container a span {
    position: absolute;
    display: block;
}

.wrap {
    width: 100%;
    min-width: 562px;
    background: #fafafa;
    border-radius: 8px;
    box-shadow: 0 5px 8px 0 rgba(0,0,0,.4);
}

.toolbar {
    width: 100%;
}

.editor_container button {
    width: 30px;
    height: 30px;
    border-radius: 3px;
    background: darkmagenta;
    box-sizing: border-box;
    padding: 0;
    font-size: 20px;
    color: #000;
    cursor: pointer;
}

#bold{
    font-weight: bold;
}
#italic{
    font-style: italic;
}
#underline{
    text-decoration-line: underline;
}

.editor_container button:hover {
    border: 1px solid #a6a6a6;
    color: #777;
}

#bold,
#italic,
#underline {
    font-size: 18px;
}

#align-right {
    margin-right: 17px;
    background-image: linear-gradient(rgba(0, 0, 255, 0.5), rgba(255, 255, 0, 0.5)),
    url("../resource/align_right.jpg");
    background-position: 0px 0px;
    background-size: 100%;
}

#align-center{
    background-image: linear-gradient(rgba(0, 0, 255, 0.5), rgba(255, 255, 0, 0.5)),
        url("../resource/align_center.jpg");
    background-position: 0px 0px;
    background-size: 100%;
}
#align-left {
    margin-left: 17px;
    background-image: linear-gradient(rgba(0, 0, 255, 0.5), rgba(255, 255, 0, 0.5)),
        url("../resource/align_left.jpg");
    background-position: 0px 0px;
    background-size: 100%;
}

.editor_container select {
    height: 24px;
    font-size: 15px;
    font-weight: bold;
    color: #444;
    background: #fcfcfc;
    border: 1px solid #a6a6a6;
    border-radius: 3px;
    margin: 0;
    outline: none;
    cursor: pointer;
}

.editor_container select > option {
    font-size: 15px;
    background: #fafafa;
}

#fonts {
    width: 140px;
}

.editor {
    position: relative;
    width: 100%;
    height: 60vh;
    margin: 0 auto;
    padding: 20px;
    background: #fcfcfc;
    border-radius: 3px;
    box-shadow: inset 0 0 8px 1px rgba(0,0,0,.2);
    box-sizing: border-box;
    overflow: hidden;
    word-break: break-all;
    outline: none;
}

#submit_btn{
    font-size: 5vb;
    text-decoration-line: none;
    border: 10px #03e9f4;
    border-radius: 20%;
    background: pink;
    display: flex;
    justify-content: center;
    align-content: center;
    min-height: 20px;
}

#submit_btn:hover{
    background: linear-gradient(pink, palegreen);
}
