html {
  height: 100%;
}
body {
  margin:0;
  padding:0;
  font-family: sans-serif;
  background: linear-gradient(#141e30, #243b55);
}

.diary_container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: rgba(0,0,0,.5);
  box-shadow: 0 15px 25px rgba(0,0,0,.6);
  border-radius: 10px;
  color: #fff;
  text-align: center;
  font-size: 2vb;
}

.question_box{
  position: relative;
  display: inline-flex;
  color: #03e9f4;
  font-size: 3vb;
  text-decoration: none;
  transition: .5s;
  justify-content: center;
  letter-spacing: 4px
}


#question_answer {
  white-space: break-spaces;
  display: inherit;
  font-size: 16px;
  color: #fff;
  border: 1px solid #fff;
  background: #e1e1dc;
  width: 100%;
  overflow-wrap: break-word;
}

